import appRoutes from "shared/constants/appRoutes";
import { getLastPartInTitleCase } from "shared/helpers/string";
import tempIcon from "shared/images/thermometer.svg";
import measureTapeIcon from "shared/images/measure-tape.svg";

export const subPagesMap = new Map([
  [
    appRoutes.toolsConvertTemp.path,
    {
      name: getLastPartInTitleCase(appRoutes.toolsConvertTemp.path, "/"),
      icon: tempIcon,
      description: appRoutes.toolsConvertTemp.title
    }
  ],
  [
    appRoutes.toolsConvertLength.path,
    {
      name: getLastPartInTitleCase(appRoutes.toolsConvertLength.path, "/"),
      icon: measureTapeIcon,
      description: appRoutes.toolsConvertLength.title
    }
  ]
]);

const convertConstants = {
  subPagesMap
};

export default convertConstants;
